$mars-deepspace: #020914
$mars-primary: #FF8177
	
*
	margin: 0
	padding: 0

body
	background: $mars-deepspace
	font-family: 'Barlow', sans-serif

#three-container
	position: relative
	width: 100%
	height: 100vh
	margin: 0 auto
	overflow: hidden

	canvas
		animation: three-come-in 3s ease-out forwards
		transform: scale(1.5)
		opacity: 0

		@keyframes three-come-in
			100%
				transform: scale(1)
				opacity: 1

	span
		position: absolute
		color: white
		top: 50%
		left: 50%
		transform: translate(-50%,-50%)
		font-size: 18px
		text-align: center

		strong
			display: block

